<template>
  <div class="container public">
    <div class="row justify-content-center">
      <div class="form">
        <form @submit.prevent="submitForm">
          <div v-show="errorMessage" class="alert alert-danger failed">{{ errorMessage }}</div>
          <div class="form-group">
            <label for="password"></label>
            <input type="password" class="form-control" id="password" v-model="form.password">
            <div class="field-error" v-if="$v.form.password.$dirty">
              <div style="color:red;" v-if="!$v.form.password.required"> </div>
              <div style="color:red;" v-if="!$v.form.password.minLength">   ( {{$v.form.password.$params.minLength.min}}   )</div>
              <div style="color:red;" v-if="!$v.form.password.maxLength">   ( {{$v.form.password.$params.maxLength.max}}   )</div>
            </div>
          </div>
          <div class="form-group">
            <label for="password2"> </label>
            <input type="password" class="form-control" id="password2" v-model="form.newpassword">
            <div class="field-error" v-if="$v.form.newpassword.$dirty">
              <div style="color:red;" v-if="!$v.form.newpassword.required"> </div>
              <div style="color:red;" v-if="!$v.form.newpassword.minLength">   ( {{$v.form.password.$params.minLength.min}}   )</div>
              <div style="color:red;" v-if="!$v.form.newpassword.maxLength">   ( {{$v.form.password.$params.maxLength.max}}   )</div>
            </div>
          </div>
          <div class="form-group">
            <label for="password3">  </label>
            <input type="password" class="form-control" id="password3" v-model="form.checknewpswd">
            <div class="field-error" v-if="$v.form.checknewpswd.$dirty">
              <div style="color:red;" v-if="!$v.form.checknewpswd.required"> </div>
              <div style="color:red;" v-if="!$v.form.checknewpswd.minLength">   ( {{$v.form.password.$params.minLength.min}}   )</div>
              <div style="color:red;" v-if="!$v.form.checknewpswd.maxLength">   ( {{$v.form.password.$params.maxLength.max}}   )</div>
              <div style="color:red;" v-if="form.newpassword !== form.checknewpswd">  </div>
            </div>
          </div>
          <button type="submit" class="btn btn-primary btn-block"> </button>
          <!-- <p class="accept-terms text-muted">  ,      </p> -->
          <p class="accept-terms text-muted">  ,    </p>
          <p class="text-center text-muted"><a class="plusB btn btn-danger text-white" @click="deleteuser"> </a></p>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import { required, minLength, maxLength } from 'vuelidate/lib/validators'
import changeService from '@/services/change'
import axios from 'axios'

export default {
  name: 'UserPage',
  data: function () {
    return {
      form: {
        password: '',
        newpassword: '',
        checknewpswd: '',
      },
      errorMessage: '',
      delete: {
        password: ''
      }
    }
  },
  validations: {
    form: {
      password: {
        required,
        minLength: minLength(6),
        maxLength: maxLength(30)
      },
      newpassword: {
        required,
        minLength: minLength(6),
        maxLength: maxLength(30)
      },
      checknewpswd: {
        required,
        minLength: minLength(6),
        maxLength: maxLength(30)
      }
    }
  },
  created () {
    axios.get('/me')
      .then(res => {
        this.$store.state.username = res.data.user.name
        // console.log(this.$store.state.username)
      })
  },
  methods: {
    submitForm () {
      this.$v.$touch()
      if (this.$v.$invalid) {
        return
      }

      changeService.register(this.form).then(() => {
        this.$swal({
          icon: "success",
          title: "  !",
        })
        this.$store.state.username = null
        this.$store.state.tempname = null
        this.$store.state.isLogin = false
        this.$router.push('/login')
      }).catch((error) => {
        this.errorMessage = 'Failed to register user. ' + error.message
      })
    },
    deleteuser () {
      this.$swal({
        title: ' ',
        text: ' ',
        icon: 'error',
        input: "password",
        showCloseButton: true,
        showCancelButton: true,
        focusConfirm: false,
        reverseButtons: true,
        focusCancel: true,
        cancelButtonText: ``,
        confirmButtonText: ``
      }).then((result) => {
        if (result.value) {
          this.delete.password = result.value
          axios.delete('/deleteuser', { data: { password: result.value } })
            .then(res => {
              this.$swal({
                icon: "success",
                title: " ."
              })
              this.$store.state.username = null
              this.$store.state.tempname = null
              this.$store.state.isLogin = false
              this.$router.push('/')
            })
        }
      })
    },
    // createCategory (nametemp) {
    //   this.newCategory.name = nametemp
    //   axios.post('/category', this.newCategory)
    //     .then(res => {
    //       this.newCategory.name = ''
    //       this.$swal({
    //         icon: "success",
    //         title: " ",
    //       })
    //       axios.get(`/${this.$store.state.username}/category`)
    //         .then(res => {
    //           this.myCategory = res.data.categoryList
    //         })
    //     })
    //     .catch(err => alert(err))
    // },
  }
}
</script>

<style lang="scss" scoped>
.public {
  margin-top: 75px
}

.accept-terms {
  margin: 20px 0 40px 0;
}
</style>
