<template>
  <div class="container public">
    <div class="row justify-content-center">
      <div class="form">
        <form @submit.prevent="submitForm">
          <div v-show="errorMessage" class="alert alert-danger failed">{{ errorMessage }}</div>
          <div class="form-group">
            <label for="username">GitHub </label>
            <input type="text" class="form-control" id="username" v-model="form.username">
            <div class="field-error" v-if="$v.form.username.$dirty">
              <div style="color:red;" v-if="!$v.form.username.required"> </div>
              <div style="color:red;" v-if="!$v.form.username.alphaNum">    </div>
              <div style="color:red;" v-if="!$v.form.username.minLength">   ( {{$v.form.username.$params.minLength.min}}   )</div>
              <div style="color:red;" v-if="!$v.form.username.maxLength">   ( {{$v.form.username.$params.maxLength.max}}   )</div>
            </div>
          </div>
          <div class="form-group">
            <label for="emailAddress"></label>
            <input type="email" class="form-control" id="emailAddress" v-model="form.emailAddress">
            <div class="field-error" v-if="$v.form.emailAddress.$dirty">
              <div style="color:red;" v-if="!$v.form.emailAddress.required"> </div>
              <div style="color:red;" v-if="!$v.form.emailAddress.email">  </div>
              <div style="color:red;" v-if="!$v.form.emailAddress.maxLength">   ( {{$v.form.emailAddress.$params.maxLength.max}}   )</div>
            </div>
          </div>
          <div class="form-group">
            <label for="password"></label>
            <input type="password" class="form-control" id="password" v-model="form.password">
            <div class="field-error" v-if="$v.form.password.$dirty">
              <div style="color:red;" v-if="!$v.form.password.required"> </div>
              <div style="color:red;" v-if="!$v.form.password.minLength">   ( {{$v.form.password.$params.minLength.min}}   )</div>
              <div style="color:red;" v-if="!$v.form.password.maxLength">   ( {{$v.form.password.$params.maxLength.max}}   )</div>
            </div>
          </div>
          <button type="submit" class="btn btn-primary btn-block"></button>
          <p class="accept-terms text-muted">  ,      </p>
          <p class="text-center text-muted">  ? <a href="/login"></a></p>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import { required, email, minLength, maxLength, alphaNum } from 'vuelidate/lib/validators'
import registrationService from '@/services/registration'
import { mapActions } from 'vuex'

export default {
  name: 'RegisterPage',
  data: function () {
    return {
      form: {
        username: '',
        emailAddress: '',
        password: ''
      },
      errorMessage: '',
    }
  },
  validations: {
    form: {
      username: {
        required,
        minLength: minLength(2),
        maxLength: maxLength(50),
        alphaNum
      },
      emailAddress: {
        required,
        email,
        maxLength: maxLength(100)
      },
      password: {
        required,
        minLength: minLength(6),
        maxLength: maxLength(30)
      }
    }
  },
  methods: {
    ...mapActions(['introduce']),
    submitForm () {
      this.$v.$touch()
      if (this.$v.$invalid) {
        return
      }

      registrationService.register(this.form).then(() => {
        this.$swal({
          icon: "success",
          title: " !",
        })
        this.$router.push({ name: 'login' })
      }).catch((error) => {
        this.errorMessage = 'Failed to register user. ' + error.message
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.public {
  margin-top: 75px
}

.accept-terms {
  margin: 20px 0 40px 0;
}
</style>
